import styled from 'styled-components'

const Caption = styled.div`
    margin: 0 auto;
    padding: 15px;
    max-width: 650px;
    text-align: center;
    background-color: #fff;
    borderTop: 1px solid #f7f7f7;
    color: #333;
`;

export default Caption;


